@import "fonts/fonts.css";
body {
    background-color: rgba(0, 0, 0, 0.65);
}

.App{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.Header, .PointName, .PointText{
    font-family: Imperial;
}

.Month, .Year, .Text{
    font-family: Pobeda;
}

@media(max-width: 1000px){
    .App{
        width: 100%;
        display: flex;
        flex-direction: column;
    }
}